body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /*font-size: calc(10px + 2vmin);*/
    margin: 0.2em;
}

.stepButton {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.App-header .small {
    font-size: 12px;
}

.d3body {
    border: 1px solid #ccc !important;
    border-radius: 16px;
    padding: 0.01em 16px;
    width: 98%;
    display: inline-block;
}

.matrix {
    width: 23%;
    float: left;
    position: absolute;
}

.Main-container {
    padding-left: 10px;
    padding-right: 10px;
}

div.tooltip {
    position: absolute;
    text-align: center;
    width: 60px;
    height: 28px;
    padding: 2px;
    font: 12px sans-serif;
    background: lightsteelblue;
    border: 0px;
    border-radius: 8px;
    pointer-events: none;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


/* Put your custom styles here */

text {
    pointer-events: none;
  }
  
  .node:hover {
    stroke: #999;
    stroke-opacity: .6;
    stroke-width: 4px;
  }
  
  .link {
    stroke: #999;
    stroke-opacity: .6;
  }

  .button rect {
    stroke: #999faa; /* navy 40% */
    stroke-width: 2px;
  }
  
  .button rect.pressed {
    fill: #000f2b; /* navy 100% */
  }
  
  .button #gradient-start {
    stop-color: #999faa; /* navy 40% */
    stop-opacity: 1;
  }
  
  .button #gradient-stop {
    stop-color: #4d576b; /* navy 70% */
    stop-opacity: 1;
  }
  
  .button #gradient-start.active, .button #gradient-start.pressed {
    stop-color: #4d576b; /* navy 70% */
  }
  
  .button #gradient-stop.active, .button #gradient-stop.pressed {
    stop-color: #000f2b; /* navy 100% */
  }
  
  .button text {
    font-size: 30px;
    fill: #eee;
    pointer-events: none;
    text-anchor: middle;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
  }
